import React from 'react'
import TextField from '@mui/material/TextField';
import { UserContext } from '../context/user-context';

const Admin = () => {
    const {authorized, setAuthorized} = React.useContext(UserContext);
    const [key, setKey] = React.useState('');
    var secretKey = process.env.REACT_APP_SC_KEY;

    const handleKeyChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setKey(event.target.value);
    };

    const handleAuthorization = (e: any) => {
        e.preventDefault();
        if (key === secretKey) {
            setAuthorized(true);
            localStorage.setItem('authorized', 'true');
        } else {
            setAuthorized(false);
        }
    };

    React.useEffect(() => {
        if (localStorage.getItem('authorized') === 'true') {
            setAuthorized(true);
        }
    }, []);

    return (
        <div className='page-container'>
            <div className='w-full flex justify-center items-center' style={{height: 'calc(100vh - 65px)'}}>
                <div className='flex flex-col'>
                    {!authorized ? <form onSubmit={handleAuthorization}>
                        <TextField 
                            id="key" 
                            label="Enter key" 
                            variant="outlined" 
                            type='password'
                            value={key}
                            onChange={handleKeyChange}
                        />
                    </form> : <div className='text-green-500 text-sm'>Authorized successfully</div>}
                </div>
            </div>
        </div>
    )
}

export default Admin