import React, { createContext, ReactNode } from 'react';
import { fetchAllData } from '../data/fetch_data';
import { ALL_DATA } from '../data/all_data';
import { ALL } from 'dns';
import { debounce } from 'lodash';

interface ProductProviderProps {
    children: ReactNode;
}

interface ProductContextProps {
    products: IProduct[];
    setProducts: React.Dispatch<React.SetStateAction<IProduct[]>>;
    search: (query: string) => void;
    resetSearch: () => void;
    searchedProducts: IProduct[];
    setSearchedProducts: React.Dispatch<React.SetStateAction<IProduct[]>>;
    inputValue: string;
    setInputValue: React.Dispatch<React.SetStateAction<string>>;
    productFields: IProductField;
    handleInputChange: (propertyName: string, value: string | number) => void,
    updateProduct: (productId: string, newProductData: IProductField) => void,
}

interface IProductField {
    id: string;
    labelName: string;
    brand: string;
    metal: string;
    description: string;
    packaging: string;
    price: number;
    salePrice: number;
    popularity: number;
}

export const ProductContext = createContext<ProductContextProps>({
    products: [],
    setProducts: () => {},
    search: () => {},
    resetSearch: () => {},
    searchedProducts: [],
    setSearchedProducts: () => {},
    inputValue: '',
    setInputValue: () => {},
    productFields: {
        id: '',
        labelName: '',
        brand: '',
        metal: '',
        description: '',
        packaging: '',
        price: 0,
        salePrice: 0,
        popularity: 0,
    },
    handleInputChange: () => {},
    updateProduct: () => {},
});

export const ProductProvider: React.FC<ProductProviderProps> = ({ children }) => {
    const [products, setProducts] = React.useState<IProduct[]>([]);
    const [searchedProducts, setSearchedProducts] = React.useState<IProduct[]>([]);
    const [inputValue, setInputValue] = React.useState('');
    const [productFields, setProductFields] = React.useState<IProductField>({
        id: '',
        labelName: '',
        brand: '',
        metal: '',
        description: '',
        packaging: '',
        price: 0,
        salePrice: 0,
        popularity: 0,
    });

    const handleInputChange = (propertyName: string, value: string | number) => {
        setProductFields({
            ...productFields,
            [propertyName]: value,
        });
    };

    const updateProduct = (productId: string, newProductData: IProductField) => {
        const updatedProducts = ALL_DATA.map((product) => {
          if (product.id === productId) {
            return { ...product, ...newProductData };
          }
          return product;
        });
        setProducts(updatedProducts);
      
        const updatedOriginalData = ALL_DATA.map((product) => {
          if (product.id === productId) {
            return { ...product, ...newProductData };
          }
          return product;
        });
      
        localStorage.setItem('products', JSON.stringify(updatedOriginalData));
    };

    React.useEffect(() => {
        const sortedData = ALL_DATA.sort((a, b) => {
            if (a.labelName && b.labelName) {
                return a.labelName.localeCompare(b.labelName);
            }
            return 0;
        });
        setProducts(sortedData);
        setSearchedProducts(sortedData);
    }, []);

    const search = React.useCallback(
        debounce((query) => {
            const filteredProducts = products.filter((product) => {
                return product.labelName?.toLowerCase().includes(query.toLowerCase());
            });
            setSearchedProducts(filteredProducts);
        }, 1000),
        [searchedProducts, setInputValue, setProducts]
    );

    const resetSearch = () => {
        setSearchedProducts(searchedProducts);
        setInputValue('');
    };

    return (
        <ProductContext.Provider value={{ 
            products,
            setProducts,
            search,
            resetSearch,
            searchedProducts,
            setSearchedProducts,
            inputValue,
            setInputValue,
            productFields,
            handleInputChange,
            updateProduct,
        }}>
            {children}
        </ProductContext.Provider>
    );
};